import { Component, OnInit } from '@angular/core';
import { FormControl } from '@angular/forms';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import {TablesDataService} from '../../../table/components/data-table/tablesData.service';
import {DataTableComponent} from '../../../table/components/data-table/data-table.component';
import {Router} from '@angular/router';

@Component({
  selector: 'app-form-layouts',
  templateUrl: './form-layouts.component.html',
  styleUrls: ['./form-layouts.component.scss'],
  providers : [TablesDataService,DataTableComponent]
})
export class FormLayoutsComponent implements OnInit {

  name = new FormControl('');
  registerForm: FormGroup;
  submitted = false;

  constructor(private _tablesDataService: TablesDataService, private formBuilder: FormBuilder, private dataTableComponent : DataTableComponent,private router: Router) { }

  ngOnInit(): void {
    this.registerForm = this.formBuilder.group({
      firstName: ['', Validators.required],
      lastName: ['', Validators.required],
      email: ['', [Validators.required, Validators.email]],
      gitrepo: ['', [Validators.required]],
      buildScript: ['', [Validators.required]],
      domain: ['', [Validators.required]],
      acceptTerms: [false, Validators.requiredTrue]
    });
  }
  get f() { return this.registerForm.controls; }

  onSubmit() {
    this.submitted = true;

    // stop here if form is invalid
    if (this.registerForm.invalid) {
      return;
    }
    this.dataTableComponent.changeData();
    // display form values on success
    alert('SUCCESS!! :-) Request Sent !\n\n');
    this.router.navigateByUrl('pages/table/data-table-new');
  }

  onReset() {
    this.submitted = false;
    this.registerForm.reset();
  }

  newProject() {
    this.router.navigateByUrl('pages/table/data-table-new');
  }
}
